import {Store} from 'react-chrome-redux';

const proxyStore = new Store({portName: 'background_data', extensionId: null});

const MAX_HISTORY_WORD_LENGTH = 100;

class WebSearchHistory {
	constructor() {
		this.isProxyStoreInitialized = false;
		this.pendingCheckEnabledCallbacks = [];
		this.isHistoryEnable = false;
		this.isHistoryCleared = false;

		proxyStore.subscribe(() => {
			if (!this.isProxyStoreInitialized) {
				this.isProxyStoreInitialized = true;

				const extensionSetting = proxyStore.getState().data.ExtensionSetting;
				if (extensionSetting && extensionSetting.IsSearchHistoryStored) {
					this.isHistoryEnable = extensionSetting.IsSearchHistoryStored;
					if (this.isHistoryEnable) {
						for (let cb of this.pendingCheckEnabledCallbacks) {
							cb();
						}
					}
				}
				this.pendingCheckEnabledCallbacks = null;
			}

			this.handleClearHistoryChanges();
		});
	}

	handleClearHistoryChanges() {
		let history = proxyStore.getState().data.history;
		if (history) {
			if (history.length == 0) {
				if (this.isHistoryCleared) {
					proxyStore.dispatch({ type: 'SET_CLEAR_HISTORY_FLAG', data: '1' });
					this.isHistoryCleared = false;
					platform_api_root.runtime.sendMessage({ method: "clearServerHistory"});
				}
			} else {  // non-empty history case.
				if (proxyStore.getState().data.clearHistory == "1") {
					proxyStore.dispatch({ type: 'SET_CLEAR_HISTORY_FLAG', data: '0' });
				}
			}
		}
	}

	checkWebHistoryIsEnabled(cb) {
		if (this.isProxyStoreInitialized) {
			if (this.isHistoryEnable) {
				cb();
			}
		} else {
			if (this.pendingCheckEnabledCallbacks.indexOf(cb) == -1) {
				this.pendingCheckEnabledCallbacks.push(cb);
			}
		}
	}

	clearHistory() {
		if (!this.isHistoryEnable) {
			return;
		}

		this.isHistoryCleared = true;
		proxyStore.dispatch({ type: 'CLEAR_HISTORY' });
	}

	getHistoryWords(maxCount) {
		if (!this.isHistoryEnable) {
			return [];
		}

		const savedHistoryWords = proxyStore.getState().data.history;
		if (savedHistoryWords) {
			return savedHistoryWords.slice(-1 * maxCount);
		}
		return [];
	}

	addNewWord(word) {
		if (!this.isHistoryEnable || !word || word.length == 0) {
			return;
		}

		proxyStore.dispatch({
			type: 'ADD_HISTORY',
			data: {
				local: true,
				input: word.substr(0, MAX_HISTORY_WORD_LENGTH)
			}
		});
	}
}

export const webSearchHistory = new WebSearchHistory();
