import {RSuggest} from './suggest';
import {webSearchHistory} from './websearchhistory';

const HISTORY_ROW_KEYWORD_CLASS = "history-row-keyword";
const HISTORY_SEPARATOR_ID = 'websearch-history-separator';
const CLEAR_HISTORY_BUTTON_ROW_ID = 'websearch-history-clearbtnrow';

class PopupRSuggest extends RSuggest {
	constructor() {
		super();
		this.numberOfFocus = 0;
		this.numberOfHistoryWordToBeDisplayed = 5;
		this.isHistoryClicked = false;
	}

	preInit() {
		// The container for suggest div is hardcoded in popup.html. It is directly under
		// root element. This is needed for zindex positioning and should not conflict with other
		// div position
		this.suggestDivPos = document.getElementById("autoCompletePopUp");
		return this.suggestDivPos != null;
	}

	createSuggestDiv() {
		super.createSuggestDiv();
		webSearchHistory.checkWebHistoryIsEnabled(
			() => this.appendHistoryElems());
	}

	appendHistoryElems() {
		for (let i = 0; i < this.numberOfHistoryWordToBeDisplayed; ++i) {
			this.addNewRow();
		}

		const clearHistoryButton = document.createElement("span");
		clearHistoryButton.textContent = "";
		clearHistoryButton.addEventListener("mousedown", (e) => {
			this.mouseDownWasAtElem = e.currentTarget;
			this.mouseState = 1;
			e.preventDefault();
		});
		clearHistoryButton.addEventListener("mouseup", (e) => {
			if (this.mouseDownWasAtElem == e.currentTarget) {
				this.clearHistory();
			}
			this.mouseState = 2;
			e.preventDefault();
		});
		clearHistoryButton.addEventListener("mouseout", (e) => {
			this.mouseState = 0;
		});

		const clearHistoryButtonRow = document.createElement("div");
		clearHistoryButtonRow.setAttribute("id", CLEAR_HISTORY_BUTTON_ROW_ID);
		clearHistoryButtonRow.addEventListener("mouseover", (e) => {
			this.over(null);
		});
		clearHistoryButtonRow.appendChild(clearHistoryButton);

		this.suggest.appendChild(clearHistoryButtonRow);
	}

	clearHistory() {
		const allHistoryKeywordElemsLiveCollection =
			this.suggest.getElementsByClassName(HISTORY_ROW_KEYWORD_CLASS);
		const historyRowsCount = allHistoryKeywordElemsLiveCollection.length;
		while (allHistoryKeywordElemsLiveCollection.length > 0) {
			const firstHistoryKeywordElem =
				allHistoryKeywordElemsLiveCollection[0];
			firstHistoryKeywordElem.parentElement.style.display = "none";
			firstHistoryKeywordElem.classList.remove(HISTORY_ROW_KEYWORD_CLASS);
		}

		this.removeHistorySeparator();
		document.getElementById(CLEAR_HISTORY_BUTTON_ROW_ID).style.display = "none";

		webSearchHistory.clearHistory();

		this.maxIndex -= historyRowsCount;
		if (this.maxIndex == 0) {  // Nothing to show.
			this.hide();
		}
	}

	addHistorySeparator(beforeRowElem) {
		const separatorElem = document.createElement("div");
		separatorElem.setAttribute("id", HISTORY_SEPARATOR_ID);
		beforeRowElem.parentElement.insertBefore(separatorElem, beforeRowElem);
	}

	removeHistorySeparator() {
		const historySeparatorElem =
			document.getElementById(HISTORY_SEPARATOR_ID);
		if (historySeparatorElem) {
			historySeparatorElem.remove();
		}
	}

	displayWithHistory(forceShow) {
		// Append history rows to the current suggestions. This code assumes
		// that maxIndex equals to the number of suggestions (i.e. it must be
		// called after the control is filled by suggestions and has no history
		// words yet.)
		this.removeHistorySeparator();

		const historyWords = webSearchHistory.getHistoryWords(
								this.numberOfHistoryWordToBeDisplayed);
		const historyWordsCount = (historyWords) ? historyWords.length : 0;

		const allKeywordElems =
			this.suggest.getElementsByClassName(this.cssKeyword);
		for (let i = 0; i < allKeywordElems.length; ++i) {
			const keywordElem = allKeywordElems[i];
			if (i < this.maxIndex) {
				// It is a suggestion.
				keywordElem.classList.remove(HISTORY_ROW_KEYWORD_CLASS);
			} else if (i >= this.maxIndex && i < this.maxIndex + historyWordsCount) {
				// It is a history word.
				keywordElem.classList.add(HISTORY_ROW_KEYWORD_CLASS);
				keywordElem.textContent = historyWords[i - this.maxIndex];
				keywordElem.parentElement.setAttribute("class", this.cssNoSelect);
				keywordElem.parentElement.style.display = "block";
			} else {
				// It is a not-used line -> hide it.
				keywordElem.classList.remove(HISTORY_ROW_KEYWORD_CLASS);
				keywordElem.parentElement.setAttribute("class", this.cssNoSelect);
				keywordElem.parentElement.style.display = "none";
			}
		}

		// Append a separator before the first history word (if there are
		// suggestions above).
		if (this.maxIndex > 0 && historyWordsCount > 0) {
			this.addHistorySeparator(
				allKeywordElems[this.maxIndex].parentElement);
		}

		const clearHistoryRowElem =
			document.getElementById(CLEAR_HISTORY_BUTTON_ROW_ID);
		if (clearHistoryRowElem) {
			clearHistoryRowElem.style.display =
				(historyWordsCount > 0) ? "block" : "none";
		}

		this.maxIndex += historyWordsCount;

		if (this.maxIndex == 0) {  // Nothing to show.
			this.hide();
			return;
		}

		if (forceShow) {
			this.suggest.style.display = "block";
		}
	}

	onRowKeywordLaunch(rowKeywordElem) {
		if (!rowKeywordElem) {
			// If the element is not passed directly, then find the selected
			// one.
			if (this.index >= 0 && this.index < this.maxIndex) {
				const allKeywordElems =
					this.suggest.getElementsByClassName(this.cssKeyword);
				if (this.index < allKeywordElems.length) {
					rowKeywordElem = allKeywordElems[this.index];
				}
			}
		}

		this.isHistoryClicked =
			rowKeywordElem &&
			rowKeywordElem.classList.contains(HISTORY_ROW_KEYWORD_CLASS);
	}

	onEmptyInput(wasEmpty) {
		// Do not re-display while it is being empty (this function is called
		// with 100ms interval while the input is focused).
		if (this.suggest.style.display == "block" && wasEmpty) {
			return;
		}

		// Display history (if any) when the input is empty.
		// But not when user has just opened the page without any interaction
		// yet (wait until he/she does the first manual focusing/click/keypress).
		const forceShow =
			this.suggest.style.display == "none" &&
			(this.numberOfFocus > 1 || this.userInteractedWithInput);

		this.index = -1;
		this.maxIndex = 0;
		this.respInput = this.lastInput;

		this.displayWithHistory(forceShow);
	}

	doDisplayOrHideIfEmpty() {
		this.displayWithHistory(/*forceShow=*/true);
	}

	jump() {
		// Track history/suggestion usage.
		const createHiddenInputElem = function(name, value) {
			const inputElem = document.createElement('input');
			inputElem.type = "hidden";
			inputElem.name = name;
			inputElem.value = value;
			return inputElem;
		};
		if (this.isHistoryClicked) {
			this.form.appendChild(createHiddenInputElem("hs", "1"));
		} else if (this.sggstSelectNum >= 0) {
			this.form.appendChild(createHiddenInputElem("sg", "1"));
		}

		super.jump();
		webSearchHistory.addNewWord(this.input.value);
	}

	eventStart() {
		this.numberOfFocus++;
		super.eventStart();
	}
}

const rsuggest = new PopupRSuggest();
rsuggest.init();
